html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fix-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #2179c2;
}
@media (min-width: 768px) {
  .header.fixedd {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  padding: 2rem 1rem;
}
@media (min-width: 768px) {
  .header-content {
    flex-direction: row;
    padding: unset;
  }
}
.header-content h1 {
  text-align: center;
  margin-top: 0;
}
@media (min-width: 768px) {
  .header-content h1 {
    margin: 2rem;
  }
}

.principal-navegation {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 768px) {
  .principal-navegation {
    flex-direction: row;
    gap: 2rem;
  }
}
.principal-navegation a {
  color: #ffffff;
  margin: auto;
  font-size: 2.4rem;
  text-align: center;
}
.principal-navegation a:hover {
  color: #ffd428;
}

.video {
  position: relative;
  max-height: 70rem;
  height: 40rem;
  overflow: hidden;
}
@media (min-width: 768px) {
  .video {
    min-height: unset;
    height: unset;
  }
}
.video video {
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.overlay {
  z-index: 9;
  position: absolute;
  background: rgb(189, 6, 103);
  background: linear-gradient(90deg, rgba(189, 6, 103, 0.4498132016) 0%, rgba(185, 10, 217, 0.0856675434) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  color: #ffffff;
}
.overlay h2 {
  font-weight: 700;
  font-size: 3rem;
  margin-bottom: 1rem;
}
@media (min-width: 480px) {
  .overlay h2 {
    font-size: 5rem;
  }
}
@media (min-width: 768px) {
  .overlay h2 {
    font-size: 7rem;
  }
}
.overlay p {
  background-color: #f53756;
  display: inline-block;
  color: #ffffff;
  padding: 1rem;
  margin: 0;
  font-weight: 400;
  font-size: 1rem;
}
@media (min-width: 480px) {
  .overlay p {
    font-weight: 700;
    font-size: 2rem;
  }
}

.festival {
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    margin: 2rem auto;
    width: 90%;
  }
}
@media (min-width: 992px) {
  .festival {
    width: 70%;
    gap: 4rem;
    margin: 2rem auto;
  }
}

.image {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  margin-top: 1rem;
}
.image img {
  max-width: 100%;
}
@media (min-width: 992px) {
  .image img {
    max-width: 100%;
  }
}

.festival-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0 1rem;
}
@media (min-width: 480px) {
  .festival-content h3 {
    text-align: center;
  }
}
@media (min-width: 768px) {
  .festival-content h3 {
    text-align: unset;
  }
}
.festival-content .date {
  color: #f53756;
  font-weight: 700;
  margin-bottom: 0;
}

.line-up {
  background-color: #f53756;
  padding-top: 3rem;
  padding-bottom: 3rem;
}
.line-up h3 {
  text-align: center;
  color: #ffffff;
}
.line-up .day {
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 5rem;
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .scenes-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.scene {
  padding-top: 2rem;
}
.scene .scene-name {
  text-align: center;
}
.scene .scene-name:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.bg-green {
  background-color: #2179c2;
}
.bg-green li:nth-child(odd) {
  background-color: #1a5e96;
}

.bg-yellow {
  background-color: #ffd428;
}
.bg-yellow li:nth-child(odd) {
  background-color: #f4c300;
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  font-weight: 700;
  padding: 2.5rem;
}
.calendar li span {
  font-weight: 400;
}

.edm .scene-name:after {
  background-image: url(../img/icono_edm.webp);
}

.rock .scene-name:after {
  background-image: url(../img/icono_rock.webp);
}

.gallery {
  padding: 5rem autp;
}
.gallery h3 {
  text-align: center;
}

.gallery-container {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 auto;
  width: 80%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .gallery-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 992px) {
  .gallery-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1rem;
  }
}

.overl {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overl picture {
  display: block;
  max-width: 80rem;
}

.btn-close {
  text-align: center;
  width: 4rem;
  height: 4rem;
  border-radius: 5rem;
  color: #000000;
  background-color: #ffffff;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-close:hover {
  cursor: pointer;
}

.tickets {
  background-color: #2179c2;
  padding: 2rem;
  margin-top: 3rem;
  width: 100%;
}
.tickets h3 {
  text-align: center;
  color: #ffffff;
}

.plans-container {
  margin: 0 auto;
}
.plans-container ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 480px) {
  .plans-container {
    width: 90%;
  }
}
@media (min-width: 768px) {
  .plans-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    width: 70%;
  }
}

.plan h4 {
  text-align: center;
  color: #ffffff;
}
.plan ul {
  background-color: #ffffff;
}
.plan li {
  padding: 1.5rem;
  border-bottom: 1px solid #e1e1e1;
}
.plan li:nth-child(1) {
  background-color: #f53756;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
}
.plan li:last-child {
  background-color: #752f97;
  color: #ffffff;
  text-align: center;
  border-bottom: unset;
}

.footer {
  background-color: #f53756;
  padding: 2rem;
}
.footer p {
  margin: 0;
  text-align: center;
  color: #ffffff;
}

